.header{
  height: 50px;
    width: 100%;
    display: flex;
    box-shadow: 5px 5px 20px #e1e1e3;
    position: fixed;
    background-color: white;
    top: 0;
    left: 0;
}
.left-header{
  height: auto;
  width: 24%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.left-header img{
  height: auto;
  width: 56.5%;
}

.center-header{
  height: auto;
  width: 53%;
  display: flex;
  justify-content: center;
  justify-content: space-evenly;
  align-items: center;
  padding: 2%;
  cursor: pointer;
}
.center-header p{
  font-size: 13px !important;
  font-weight: 500;
  /* word-spacing: 15px; */
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif,"sans-serif",Roboto,Oxygen,Ubuntu,Cantarell,Fira Sans,Droid Sans,Helvetica Neue!important;
}
.right-header{
  height: auto;
  width: 20%;
  display: flex;
  align-items: center;
}
.login{
  height: auto;
  width: auto;
  display: flex;
  font-size: 13px;
  justify-content: space-around;
  font-family: 'arial';
  cursor: pointer;
}
.locker, .contact{
  height: auto;
  width: 14px;
  display: flex;
  margin-right: 2px;
  margin-left: 5px;
}
.section1{
  height: auto;
  width: 100%;
  display: flex;
  margin-top: 70px;
  align-items: center;
}
.section1-right{
  height: auto;
  width: 62%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}


.search-bar{
  height: auto;
    width: 80%;
    display: flex;
    background-color: white;
    padding: 4px;
    border-radius: 50px;
    border: 5px solid lightgray;
    align-items: center;
    justify-content: space-between;
    margin-top: 24px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif,"sans-serif",Roboto,Oxygen,Ubuntu,Cantarell,Fira Sans,Droid Sans,Helvetica Neue!important;
}
.search{
  height: auto;
  width: auto;
  background-color: #213858;
  color: white;
  padding: 3% 3.5%;
  font-size: 1rem;
  border-radius: 50%;
}
.main-cont{
  height: auto;
  width: 100%;
  display: flex;
  margin-left: 15%;
  padding: 2%;
}
.main-cont p{
  font-size: 44px;
  font-family: 'arial';
}
.section-left{
  height: auto;
  width: 25%;
  box-shadow: 5px 5px 20px #e1e1e3;
  margin-left: 83px;
  padding: 2.5% 1%;
  border-radius: 18px;
}
.image {
  height: auto;
  width: auto;
  padding: 2%;
  display: flex;
  justify-content: center;
}
.image img{
  height: auto;
  width: 82%;
}
.second-text{
  text-align: center;
  margin-top: 2%;
  font-size: 1.1rem;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif,"sans-serif",Roboto,Oxygen,Ubuntu,Cantarell,Fira Sans,Droid Sans,Helvetica Neue!important;

}
.second-button{
  display: flex;
  margin-top: 2%;
  justify-content: center;
  
}
.second-button button {
  font-size: 0.7rem;
  padding: 7px 6px;
  border-radius: 5px;
  border: none;
  background-color: #213858;
  color: white;
  font-weight: 600;
}
.section2{
  height: 400px;
  width: 100%;
}
.section2-content{
  height: auto;
  width: 100%;
}
.section2-content h1 {
  text-align: center;
  font-family: 'arial';
  margin: 40px 0 20px;
  color: #213858;
}
.section2-content p{
  text-align: center;
  margin-top: 1%;
  color: #213858;
  font-size: 18px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif,"sans-serif",Roboto,Oxygen,Ubuntu,Cantarell,Fira Sans,Droid Sans,Helvetica Neue!important;
}
.parent-card{
  height: auto;
  width: auto;
  display: grid;
    flex-direction: column;
    grid-template-columns: auto auto auto auto;
    grid-gap: 14px 5px;
    padding: 2%;
    font-family: 'arial';
}
.main-card {
  height: auto;
    width: 90%;
    margin-top: 10%;
    border-radius: 16px;
    overflow: hidden;
    padding: 19px;
    box-shadow: 15px 15px 27px #e1e1e3, -15px -15px 27px #ffffff;
}
.main-card:hover{
  transform: scale(1.1);
  transition-duration: 0.7s;
  background-color: #F8F8F8;
  cursor: pointer;
}
.image-card {
  height: 130px;
    width: 100%;
    overflow: hidden;
}
.image-card img{
  width: 100%;
  border-radius: 5px;
}
.card-title{
  margin-top: 10px;
  font-size: 16px;
    font-weight: 600;
    color: #213858;
}
.card-content{
  margin-top: 10px;
    font-size: 13px;
    text-align: left;
    font-weight: 400;
    color: #374957;
    
}
.section3{
  height: auto;
  width: 100%;
}
.section3-title {
  height: auto;
  width: 100%;
  
  font-size: 36px;
  text-align: center;
  margin: 40px 0 20px;
  font-family: 'arial';
}
.section3-parts {
  width: 100%;
  display: flex;
}
.section3-left {
  height: 300px;
    width: 56%;
    padding: 0px 67px;
}
.section3-right{
  width: 40%;
}
.left2 {
  margin: 20px 0;
  font-family: 'arial';
  line-height: 26px;
  width: auto;
  font-size: 16px;
  padding: 0px
}
button {
  padding: 6px 16px;
  border-radius: 5px;
  cursor: pointer;
}

.right-header .header-icon {
  height: auto;
  width: 14%;
  margin-left: 2%;
  margin-right: 5px;
  background-color: lightgrey;
  padding: 3%;
  border-radius: 25%;
}

.section3-right {
  height: fit-content;
  margin-right: 20px;
  width: 40%;
  
  font-family: 'arial';
  
  box-shadow: 5px 5px 20px #e1e1e3;

    padding: 20px;
    text-align: center;
}
input {
  display: inline-block;
    width: 44%;
    margin: 15px 10px;
    padding: 10px 13px;
    border-radius: 3px;
    border: 1px solid lightgray;
}
.submit-line {
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
}
.section-right-title {
  color: #213858;
    font-size: 26px;
    font-weight: 500;
}
.are-you{
  color: gray;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif,"sans-serif",Roboto,Oxygen,Ubuntu,Cantarell,Fira Sans,Droid Sans,Helvetica Neue!important;

  font-size: 14px;
}
button{
  font-size: 14px;
    padding: 9px 17px;
    border-radius: 5px;
    border: none;
    background-color: #213858;
    color: white;
    font-weight: 600;
}
.section3-image{
  padding: 34px 6px;
}
.section3-image img{
  width: 55%;
    padding: 4px 10px;
}
.left3 button{
  border: 1px solid lightgray;
    background-color: transparent;
    color: #213858;
    font-weight: 400;
    font-size: 14px;
    margin-bottom: 10%;
}
.call-icons{
  font-size: 18px;
  margin-right: 10px;
}
.left3 button:hover{
  border-color: black;
  background-color: #F8F8F8;
}
